<template>
    <div class="globlgl globlgl--custom" >
        <div class="btns">
            <el-button class="float-l btn" @click="rebackEvent" v-show="rebackButton"></el-button>
            <el-button class="float-l btn" @click="handleIvrEntryAdd"></el-button>
            <el-button class="btn" @click="handleBatchDelete"></el-button>
            <div class="search float-r">
                <el-input class="search-input" placeholder="IVR"  :maxlength="128" icon="search" v-model="searchInput" :on-icon-click="handleIvrEntrySearch">
                </el-input>
            </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="ivrEntryData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="vlanId" label="">
                <template slot-scope="scope">
                    <el-button @click="handleivrEntryCheck(scope.$index, scope.row)" type="text">{{scope.row.vlanId}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="ivrEntryId" label=""></el-table-column>
            <el-table-column prop="ivrMenuId" label="IVR"></el-table-column>
            <el-table-column prop="ivrMenuName" label="IVR"></el-table-column>
            <el-table-column prop="digits" label="" width="120"></el-table-column>
            <el-table-column prop="operatType" label=""></el-table-column>
            <el-table-column prop="operatData" label=""></el-table-column>
            <el-table-column prop="description" label=""></el-table-column>
            <el-table-column label="" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleIvrEntryDelete(scope.$index, scope.row)"></el-button>
                    <el-button type="text" size="small" @click="handleIvrEntryEdit(scope.$index, scope.row)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--IVR-->
        <el-dialog title="IVR" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="ivrEntryForm" :rules="formRules" labelPosition="left" ref="ivrEntryForm">
                <el-form-item prop="vlanId" label="" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="ivrEntryForm.vlanId" placeholder="" :disabled="isVlanIdDisabled">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="ivrEntryId" label="" :label-width="formLabelWidth">
                    <el-input v-model.number="ivrEntryForm.ivrEntryId" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="ivrMenuName" label="IVR" :label-width="formLabelWidth">
                    <el-select v-model="ivrEntryForm.ivrMenuName" placeholder="">
                        <el-option  v-for="item in allIvrFlowMenu" :key="item.value" :label="item.label" :value="item.label">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="digits" label="" :label-width="formLabelWidth">
                    <el-input v-model="ivrEntryForm.digits" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="operatType" label="" :label-width="formLabelWidth">
                    <el-select v-model="ivrEntryForm.operatType" placeholder="">
                        <el-option  v-for="item in alloperatType" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="operatData" label="" :label-width="formLabelWidth">
                    <el-input v-model="ivrEntryForm.operatData" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="description" label="" :label-width="formLabelWidth">
                    <el-input v-model="ivrEntryForm.description" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="handleIvrEntrySave('ivrEntryForm')"> </el-button>
            </div>
        </el-dialog>
        <!--IVR-->
        <el-dialog title="IVR" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="ivrEntryEditForm" :rules="formRules" labelPosition="left" ref="ivrEntryEditForm">
                <el-form-item prop="vlanId" label="" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="ivrEntryEditForm.vlanId" placeholder="" :disabled="isVlanIdDisabled">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="ivrEntryId" label="" :label-width="formLabelWidth">
                    <el-input v-model.number="ivrEntryEditForm.ivrEntryId" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="ivrMenuName" label="IVR" :label-width="formLabelWidth">
                    <el-select v-model="ivrEntryEditForm.ivrMenuName" placeholder="">
                        <el-option  v-for="item in allIvrFlowMenu" :key="item.value" :label="item.label" :value="item.label">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="digits" label="" :label-width="formLabelWidth">
                    <el-input v-model="ivrEntryEditForm.digits" :maxlength="255" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="operatType" label="" :label-width="formLabelWidth">
                    <el-select v-model="ivrEntryEditForm.operatType" placeholder="">
                        <el-option  v-for="item in alloperatType" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="operatData" label="" :label-width="formLabelWidth">
                    <el-input v-model="ivrEntryEditForm.operatData" :maxlength="255" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="description" label="" :label-width="formLabelWidth">
                    <el-input v-model="ivrEntryEditForm.description" :maxlength="255" auto-complete="off"></el-input>
                    
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false"> </el-button>
            <el-button type="primary" @click="handleIvrEntrySave('ivrEntryEditForm')"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="IVR" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">: {{showCheck.vlanId}}</div>
            <div class="item">: {{showCheck.ivrEntryId}}</div>
            <div class="item">IVR: {{showCheck.ivrMenuId}}</div>
            <div class="item">IVR: {{showCheck.ivrMenuName}}</div>
            <div class="item">: {{showCheck.operatData}}</div>
            <div class="item">: {{showCheck.operatType}}</div>
            <div class="item">: {{showCheck.digits}}</div>
            <div class="item">: {{showCheck.description}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false"> </el-button>
            <el-button type="primary" @click="dialogFormCheck=false"> </el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    ivrEntryList,
    saveivrEntry,
    deleteivrEntryByIds,
    getAllIvrFlowMenu,
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';

  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback(new Error(''));
            }
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        var check = (rule, value, callback) => { 
            if (value == undefined || "" === value) {
                callback(new Error(''));
            }else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
      };
      return {
        //
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //
        ivrEntryData: [],
        showCheck: {}, //
        dialogFormCheck: false, //
        isVlanIdDisabled:true,
        
        multipleSelection: [],
        //
        dialogFormVisible: false,
        dialogFormEdit: false, //
        ivrEntryForm: {
          vlanId: '',
          ivrEntryId: '',
          ivrMenuId: '',
          ivrMenuName: '',
          digits: '',
          operatType: '',
          operatData: '',
          description: '',
        },
        ivrEntryEditForm: {
             vlanId: '',
             ivrEntryId: '',
             ivrMenuId: '',
             ivrMenuName: '',
             digits: '',
             operatType: '',
             operatData: '',
             description: '',
        },
        formLabelWidth: '80px',
        formRules: {
           vlanId: [{required: true,  validator: checkNumber, message:'()', trigger: 'blur'}],
           ivrEntryId: [{required: true,  validator: checkNumber, trigger: 'blur'}],
           ivrMenuId: [{validator: checkNull, trigger: 'blur'}],
           ivrMenuName: [{required: true,validator: check, message:'IVR', trigger: 'blur'}],
         
        },
        alloperatType:[{
            value: 'transfer',
            label: ''
        },{
            value: 'playback',
            label: ''
        },{
            value: 'hangup',
            label: ''
        },{
            value: 'ivr',
            label: 'ivr'
        }],
        allIvrFlowMenu:[],
        allIvrFlowMenuObj:{},
        currentUserVlanId:this.$store.baseStore.getters.getUserInfo.vlanId,
      }
    },
    created() {
      this.getivrEntryList(); //
      this.getAllIvrFlowMenu();
      this.isDisabled();
    },
    mounted(){
        var coms = this.$store.baseStore.getters.getAllCompanyInfo;
        if(coms == undefined || coms.length == 0){
            this.$store.baseStore.dispatch('getAllDeptId');
        }else{
            this.$data.allCompanyId= coms;
        }
    },
    components: {
      ElTableTreeColumn
    },
    props: {
        rebackButton: false,
        companyId: '',
    },
    watch:{
        companyId: function (val, oldVal){
            this.getivrEntryList(); 
        },
    },
    computed: {
        allCompanyId:function() {
             return this.$store.baseStore.getters.getAllCompanyInfo;
        }
    },
    methods: {
      // 
      async getivrEntryList() {
        try {
          let query = {
            admincompanyId:this.companyId,
            ivrMenuName: this.searchInput
          }
          //
          const res = await ivrEntryList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.ivrEntryData = [];
            // 
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              if(item.operatType == 'transfer'){
                item.operatType = '';
              } else if(item.operatType == 'playback'){
                item.operatType = '';
              } else if(item.operatType == 'hangup'){
                item.operatType = '';
              } else {
                item.operatType = 'ivr';
              }
              const tableItem = {
                createAt:item.createAt,
                description:item.description,
                digits:item.digits,
                ivrEntryId:item.ivrEntryId,
                ivrMenuId:item.ivrMenuId,
                ivrMenuName:item.ivrMenuName,
                operatData:item.operatData,
                operatType:item.operatType,
                updateAt:item.updateAt,
                uuid:item.uuid,
                vlanId:item.vlanId,
              }
              this.ivrEntryData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('', err);
        }
      },
      handSaveParam(args){
         if(args.vlanId == undefined || args.vlanId === '')delete args.vlanId;
         if(args.ivrEntryId == undefined || args.ivrEntryId === '')delete args.ivrEntryId;
         if(args.ivrMenuId == undefined || args.ivrMenuId === ''){
             delete args.ivrMenuId;
         }else{
             args.ivrMenuName=this.$data.allIvrFlowMenuObj[args.ivrMenuId];
         }
         return args;
      },
      isDisabled(){
        if(this.companyId!='' && this.companyId != undefined){
          this.isVlanIdDisabled = true;
        } else {
          this.isVlanIdDisabled = false;
        }
      },
      handleSizeChange(val) {
      },
      rebackEvent(){
        var args =  new Object();
        this.$emit('rebackEvent',args);
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getivrEntryList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleIvrEntrySearch() {
        this.getivrEntryList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return moment(time).format("YYYY-MM-DD HH:mm:ss")  
         }else{
            return '';
         }
         
      },
      //
      handleIvrEntryAdd() {
        var tempVlanId = this.$data.currentUserVlanId;
        if(this.companyId != undefined && this.companyId != ''){
            tempVlanId = parseInt(this.companyId);
        }
        this.ivrEntryForm = {
          vlanId: tempVlanId,
          ivrEntryId: '',
          ivrMenuId: '',
          ivrMenuName: '',
          digits: '',
          operatType: '',
          operatData: '',
          description: '',
        };
        this.dialogFormVisible = true;
      },
      //
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('', '', {
            confirmButtonText: '',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //
              const result = await deleteivrEntryByIds(data);
              if (result.resultCode == '0') {
                that.getivrEntryList();
                this.$message({
                  type: 'success',
                  message: '!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }
      },
      //
      handleIvrEntryDelete(index, row) {
        let that = this;
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //
            const result = await deleteivrEntryByIds(data);
            if (result.resultCode == '0') {
              that.getivrEntryList();
              this.$message({
                type: 'success',
                message: '!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //ivr
      async getAllIvrFlowMenu() {
        try {
            const res = await getAllIvrFlowMenu();
            if (res.resultCode == '0') {
                for (var value of res.result){
                   var temp = {};
                   temp.label = value.ivrMenuName;
                   temp.value = value.ivrMenuId;
                   this.$data.allIvrFlowMenu.push(temp);
                   this.$data.allIvrFlowMenuObj[temp.value]=temp.label;
                }
            }else {
               throw new Error(res.result)
            }
        } catch (err) {
            console.log('', err);
        }
      },
      //
      handleIvrEntryEdit(index, row) {
        if (typeof row !== 'undefined') {
            this.ivrEntryEditForm.vlanId = row.vlanId;
            this.ivrEntryEditForm.ivrEntryId = row.ivrEntryId;
            this.ivrEntryEditForm.ivrMenuId = row.ivrMenuId;
            this.ivrEntryEditForm.ivrMenuName = row.ivrMenuName;
            this.ivrEntryEditForm.digits = row.digits;
            var operatType = '';
            if(row.operatType == ''){
                operatType = 'transfer';
              } else if(row.operatType == ''){
                operatType = 'playback';
              } else if(row.operatType == ''){
                operatType = 'hangup';
              } else {
                operatType = 'ivr';
              }
            this.ivrEntryEditForm.operatType = operatType;
            this.ivrEntryEditForm.operatData = row.operatData;
            this.ivrEntryEditForm.description = row.description;
            this.ivrEntryEditForm.uuid = row.uuid;
        }
        this.dialogFormEdit = true;
      },
      //
      handleivrEntryCheck(index, row) {
        this.dialogFormCheck = true;
        this.$data.showCheck = row;
      },
      //  
      handleIvrEntrySave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //
              var args = this.$refs[formName].model;
              var params = this.handSaveParam(args);
              const result = await saveivrEntry(params);
              if (result.resultCode == '0') {
                  if(formName == 'ivrEntryForm'){//
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getivrEntryList();
                  this.$message({
                     type: 'success',
                     message: 'IVR'
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: '1'
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: '2'
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: ''
             });
            return false;
          }
        });
      },
    }
  }

</script>
<style scope lang="less">

 .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          max-width: 500px;
          overflow-x: auto;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }


</style>
<style lang="less">
    .globlgl.globlgl--custom .el-table .el-table__body-wrapper {
        overflow: auto;
    }
</style>


