<template>
    <div class="extGroupsgl">
        <div class="btns">
            <el-button class="float-l btn" @click="rebackEvent" v-show="rebackButton"></el-button>
            <el-button class="float-l btn" @click="handleExtGroupsAdd"></el-button>
            <el-button class="btn" @click="handleBatchDelete"></el-button>
            <!-- <div class="search float-r">
                <el-input class="search-input" placeholder="" icon="search" v-model="searchInput" :on-icon-click="handleExtGroupsSearch">
                </el-input>
            </div> -->
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="extGroupsData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="vlanId" label="">
                <template slot-scope="scope">
                    <el-button @click="handleextGroupsCheck(scope.$index, scope.row)" type="text">{{scope.row.vlanId}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="groupId" label="ID"></el-table-column>
            <!-- <el-table-column prop="extNbr" label=""></el-table-column> -->
            <el-table-column prop="extNumber" label=""></el-table-column>
            <!-- <el-table-column prop="priority" label=""></el-table-column> -->
            <el-table-column prop="rules" label=""></el-table-column>
            <el-table-column prop="description" label=""></el-table-column>
            <el-table-column label="" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleExtGroupsDelete(scope.$index, scope.row)"></el-button>
                    <el-button type="text" size="small" @click="handleExtGroupsEdit(scope.$index, scope.row)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="extGroupsForm" :rules="formRules" labelPosition="left" ref="extGroupsForm">
                <el-form-item prop="vlanId" label="" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="extGroupsForm.vlanId" placeholder="">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="groupId" label="ID" :label-width="formLabelWidth">
                    <el-input v-model.number="extGroupsForm.groupId" auto-complete="off"></el-input>
                </el-form-item>
                <!-- <el-form-item prop="extNbr" label="" :label-width="formLabelWidth">
                    <el-input v-model="extGroupsForm.extNbr" auto-complete="off"></el-input>
                </el-form-item> -->
                <!-- <el-form-item prop="priority" label="" :label-width="formLabelWidth">
                    <el-input v-model.number="extGroupsForm.priority" auto-complete="off"></el-input>
                </el-form-item> -->
                <el-form-item prop="rules" label="" :label-width="formLabelWidth">
                    <el-select v-model.number="extGroupsForm.rules" placeholder="">
                        <el-option  v-for="item in rulesTypeArr" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="description" label="" :label-width="formLabelWidth">
                    <el-input v-model="extGroupsForm.description"  :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="handleExtGroupsSave('extGroupsForm')"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="extGroupsEditForm" :rules="formRules" labelPosition="left" ref="extGroupsEditForm">
                <el-form-item prop="vlanId" label="" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="extGroupsEditForm.vlanId" placeholder="">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="groupId" label="ID" :label-width="formLabelWidth">
                    <el-input v-model.number="extGroupsEditForm.groupId" :disabled="true" auto-complete="off"></el-input>
                    
                </el-form-item>
                <!-- <el-form-item prop="extNbr" label="" :label-width="formLabelWidth">
                    <el-input v-model="extGroupsEditForm.extNbr" auto-complete="off"></el-input>
                    
                </el-form-item> -->
                <!-- <el-form-item prop="priority" label="" :label-width="formLabelWidth">
                    <el-input v-model.number="extGroupsEditForm.priority" auto-complete="off"></el-input>
                    
                </el-form-item> -->
                <el-form-item prop="rules" label="" :label-width="formLabelWidth">
                    <el-select v-model.number="extGroupsEditForm.rules" placeholder="">
                        <el-option  v-for="item in rulesTypeArr" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="description" label="" :label-width="formLabelWidth">
                    <el-input v-model="extGroupsEditForm.description" :maxlength="255" auto-complete="off"></el-input>
                    
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false"> </el-button>
            <el-button type="primary" @click="handleExtGroupsSave('extGroupsEditForm')"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">: {{showCheck.vlanId}}</div>
            <!-- <div class="item">: {{showCheck.extNbr}}</div> -->
            <div class="item">: {{showCheck.extNumber}}</div>
            <div class="item">ID: {{showCheck.groupId}}</div>
            <!-- <div class="item">: {{showCheck.priority}}</div> -->
            <div class="item">: {{showCheck.rules}}</div>
            <div class="item">: {{showCheck.description}}</div>
            <div class="item">: {{showCheck.createAt}}</div>
            <div class="item">: {{showCheck.updateAt}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false"> </el-button>
            <el-button type="primary" @click="dialogFormCheck=false"> </el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    extGroupsList,
    saveextGroups,
    deleteextGroupsByIds,
    getExtgCountByVlanId,
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback(new Error(''));
            }
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
      };
      return {
        //
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //
        extGroupsData: [],
        showCheck: {}, //
        dialogFormCheck: false, //

        multipleSelection: [],
        //
        dialogFormVisible: false,
        dialogFormEdit: false, //
        extGroupsForm: {
          vlanId: '',
          groupId: '',
          // extNbr: '',
          // priority: '',
          rules: '',
          description: '',
        },
        extGroupsEditForm: {
             vlanId: '',
             groupId: '',
            //  extNbr: '',
            //  priority: '',
             rules: '',
             description: '',
        },
        formLabelWidth: '80px',
        formRules: {
           vlanId: [{required: true,  validator: checkNumber, message:'()', trigger: 'blur'}],
           groupId: [{required: true,  validator: checkNumber, trigger: 'blur'}],
          //  priority: [{validator: checkNull, trigger: 'blur'}],
           rules: [{validator: checkNull, trigger: 'blur'}],
         
        },
        rulesTypeArr: [{
          value: 0,
          label: ''
        },{
          value: 1,
          label: ''
        }],
        currentUserVlanId:this.$store.baseStore.getters.getUserInfo.vlanId,
       
      }
    },
    created() {
      this.getextGroupsList(); //
    },
    mounted(){
        var coms = this.$store.baseStore.getters.getAllCompanyInfo;
        if(coms == undefined || coms.length == 0){
            this.$store.baseStore.dispatch('getAllDeptId');
        }else{
            this.$data.allCompanyId= coms;
        }
    },
    watch:{
        companyId: function (val, oldVal){
            this.getextGroupsList(); 
        },
    },
    computed: {
        allCompanyId:function() {
             return this.$store.baseStore.getters.getAllCompanyInfo;
        }
    },
    components: {
      ElTableTreeColumn
    },
    props: {
        rebackButton: false,
        companyId: '',
    },
    methods: {
      // 
      async getextGroupsList() {
        try {
          let query = {
            // extNbr: this.searchInput
            admincompanyId:this.companyId,
          }
          //
          const res = await extGroupsList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.extGroupsData = [];
            // 
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              var rules = '';
              if(item.rules==1){
                rules = '';
              }
              const tableItem = {
                createAt:item.createAt,
                description:item.description,
                // extNbr:item.extNbr,
                extNumber:item.extNumber,
                groupId:item.groupId,
                // priority:item.priority,
                rules:rules,
                updateAt:item.updateAt,
                uuid:item.uuid,
                vlanId:item.vlanId,
              }
              this.extGroupsData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('', err);
        }
      },
      handSaveParam(args){
         if(args.groupId == undefined || args.groupId === '')delete args.groupId;
        //  if(args.priority == undefined || args.priority === '')delete args.priority;
         if(args.rules == undefined || args.rules === '')delete args.rules;
         return args;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getextGroupsList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleExtGroupsSearch() {
        this.getextGroupsList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return moment(time).format("YYYY-MM-DD HH:mm:ss")  
         }else{
            return '';
         }
         
      },
      //
      handleExtGroupsAdd() {
        var tempVlanId = this.$data.currentUserVlanId;
        if(this.companyId != undefined && this.companyId != ''){
            tempVlanId = parseInt(this.companyId);
        }
        this.extGroupsForm = {
          vlanId: tempVlanId,
          groupId: '',
          // extNbr: '',
          // priority: 100,
          rules: 0,
          description: '',
        };
        this.dialogFormVisible = true;
      },
      //
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('', '', {
            confirmButtonText: '',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //
              const result = await deleteextGroupsByIds(data);
              if (result.resultCode == '0') {
                that.getextGroupsList();
                this.$message({
                  type: 'success',
                  message: '!'
                });
              } else if(result.resultCode == '91007'){
                this.$message({
                  type: 'error',
                  message: result.result,
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }
      },
      //
      handleExtGroupsDelete(index, row) {
        let that = this;
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //
            const result = await deleteextGroupsByIds(data);
            if (result.resultCode == '0') {
              that.getextGroupsList();
              this.$message({
                type: 'success',
                message: '!'
              });
            } else if(result.resultCode == '91007'){
                this.$message({
                  type: 'error',
                  message: result.result,
                });
              }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      handleExtGroupsEdit(index, row) {
        let rules = 0;
        if(row.rules == ''){
          rules = 1;
        }
        if (typeof row !== 'undefined') {
            this.extGroupsEditForm.vlanId = row.vlanId;
            this.extGroupsEditForm.groupId = row.groupId;
            // this.extGroupsEditForm.extNbr = row.extNbr;
            // this.extGroupsEditForm.priority = row.priority;
            this.extGroupsEditForm.rules = rules;
            this.extGroupsEditForm.description = row.description;
            this.extGroupsEditForm.uuid = row.uuid;
        }
        this.dialogFormEdit = true;
      },
      //
      handleextGroupsCheck(index, row) {
        this.dialogFormCheck = true;
        row.createAt=this.dateFormat(row.createAt);
        row.updateAt=this.dateFormat(row.updateAt);
        this.$data.showCheck = row;
      },
      dateFormat(time) {
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");
         }else{
            return '';
         }
         
      },
      //  
      handleExtGroupsSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //
              var args = this.$refs[formName].model;
              var vlanID = this.$store.baseStore.getters.getUserInfo.vlanId;
              if(this.$store.baseStore.getters.getUserInfo.managerLevel=='999'){
                vlanID = args.vlanId;
              }
              var params = this.handSaveParam(args);
              var extCount = await getExtgCountByVlanId({
                vlanId:vlanID,
              });
              if(extCount.result == -1 && formName == 'extGroupsForm'){
                this.$message({
                     type: 'error',
                     message: ''
                });
              } else if(extCount.result > 0 || formName == 'extGroupsEditForm'){
                const result = await saveextGroups(params);
                if (result.resultCode == '0') {
                    if(formName == 'extGroupsForm'){//
                          that.dialogFormVisible = false;
                    }else{
                      this.dialogFormEdit = false;
                    }
                    that.getextGroupsList();
                    this.$message({
                      type: 'success',
                      message: ''
                    });
                }else if(result.resultCode == '00009'){
                    this.$message({
                        type: 'error',
                        message: result.result,
                    });
                }else{
                    this.$message({
                      type: 'error',
                      message: ''
                    });
                }
              } else {
                this.$message({
                  type: 'error',
                  message: ''
                });
              }
              
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: ''
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: ''
             });
            return false;
          }
        });
      },
      rebackEvent(){
          var args =  new Object();
          this.$emit('rebackEvent',args);
      },
    }
  }

</script>
<style scope lang="less">

  .extGroupsgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .extGroupsgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>



