<template>
    <div class="globlgl">
        <div class="btns">
            <el-button class="float-l btn" @click="rebackEvent" v-show="rebackButton"></el-button>
            <el-button class="float-l btn" @click="handleCallerBlackListsAdd"></el-button>
            <el-button class="btn" @click="handleBatchDelete"></el-button>
            <el-button class="btn" @click="blackImportDialogVisible = true"></el-button>
            <div class="search float-r">
             <el-input class="search-input" placeholder="" icon="search" v-model="searchInput" :on-icon-click="handleCallerBlackListsSearch">
             </el-input>
               
            </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="callerBlackListsData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="vlanId" label="">
                <template slot-scope="scope">
                    <el-button @click="handlecallerBlackListsCheck(scope.$index, scope.row)" type="text">{{scope.row.vlanId}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="blacklistsId" label=""></el-table-column>
            <el-table-column prop="prefix" label=""></el-table-column>
            <el-table-column prop="blackType" label=""></el-table-column>
            <el-table-column prop="describe" label=""></el-table-column>
            <el-table-column label="" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleCallerBlackListsDelete(scope.$index, scope.row)"></el-button>
                    <el-button type="text" size="small" @click="handleCallerBlackListsEdit(scope.$index, scope.row)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="callerBlackListsForm" :rules="formRules" labelPosition="left" ref="callerBlackListsForm">
                <el-form-item prop="vlanId" label="" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="callerBlackListsForm.vlanId" placeholder="">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="blackType" label="" :label-width="formLabelWidth">
                    <el-input v-model.number="callerBlackListsForm.blackType" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="prefix" label="" :label-width="formLabelWidth">
                    <el-input v-model="callerBlackListsForm.prefix" :maxlength="64" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="describe" label="" :label-width="formLabelWidth">
                    <el-input v-model="callerBlackListsForm.describe" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="handleCallerBlackListsSave('callerBlackListsForm')"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="callerBlackListsEditForm" :rules="formRules" labelPosition="left" ref="callerBlackListsEditForm">
                <el-form-item prop="vlanId" label="" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="callerBlackListsEditForm.vlanId" placeholder="">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="blackType" label="" :label-width="formLabelWidth">
                    <el-input v-model.number="callerBlackListsEditForm.blackType" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="prefix" label="" :label-width="formLabelWidth">
                    <el-input v-model="callerBlackListsEditForm.prefix" :maxlength="64" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="describe" label="" :label-width="formLabelWidth">
                    <el-input v-model="callerBlackListsEditForm.describe"  :maxlength="255" auto-complete="off"></el-input>
                    
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false"> </el-button>
            <el-button type="primary" @click="handleCallerBlackListsSave('callerBlackListsEditForm')"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">: {{showCheck.vlanId}}</div>
            <div class="item">: {{showCheck.blackType}}</div>
            <div class="item">: {{showCheck.prefix}}</div>
            <div class="item">: {{showCheck.describe}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false"> </el-button>
            <el-button type="primary" @click="dialogFormCheck=false"> </el-button>
            </div>
        </el-dialog>
        <el-dialog title="" class="w-500 dialog--import-file" :visible.sync="blackImportDialogVisible" :close-on-click-modal="2==1">
            <import-file-form :importAPI="blackImportListFileAPI" :downloadTemplateAPI="blackTemplateDownloadAPI" 
                :setUploadParams="setUploadParams" :failDetailAPI="blackImportFailedFileAPI" :dialogParent="blackImportDialogVisible"
                @cancel="blackImportDialogVisible = false" @successCallback="importSuccessCallback">
             </import-file-form>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    callerBlackListsList,
    savecallerBlackLists,
    deletecallerBlackListsByIds,

    callerBlackListsTemplateDownload,
    callerBlackListsImport,
    blackImportFailedFileAPI,
  }from '@/api/getData';
    
  import importFileForm from '../call/importFileForm.vue'
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback(new Error(''));
            }
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
      };
      return {
        //
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //
        callerBlackListsData: [],
        showCheck: {}, //
        dialogFormCheck: false, //

        multipleSelection: [],
        //
        dialogFormVisible: false,
        dialogFormEdit: false, //
        callerBlackListsForm: {
          vlanId: 0,
          blackType: '',
          prefix: '',
          describe: '',
        },
        callerBlackListsEditForm: {
          vlanId: 0,
          blackType: '',
          prefix: '',
          describe: '',
        },
        formLabelWidth: '80px',
        formRules: {
           vlanId: [{required: true,validator:checkNumber, message:'()',trigger: 'blur'}],
           blackType: [{validator: checkNull, trigger: 'blur'}],
         
        },

        blackImportDialogVisible: false,
        blackImportListFileAPI: callerBlackListsImport,
        blackTemplateDownloadAPI: callerBlackListsTemplateDownload,
        blackImportFailedFileAPI: blackImportFailedFileAPI,
        currentUserVlanId:this.$store.baseStore.getters.getUserInfo.vlanId,
      }
    },
    created() {
      this.getcallerBlackListsList(); //
    },
    mounted(){
         var coms = this.$store.baseStore.getters.getAllCompanyInfo;
        if(coms == undefined || coms.length == 0){
            this.$store.baseStore.dispatch('getAllDeptId');
        }else{
            this.$data.allCompanyId= coms;
        }
    },
    components: {
      importFileForm: importFileForm,
    },
    props: {
        rebackButton: false,
        companyId: '',
    },
    watch:{
        companyId: function (val, oldVal){
            this.getcallerBlackListsList(); 
        },
    },
    computed: {
        allCompanyId:function() {
             return this.$store.baseStore.getters.getAllCompanyInfo;
        }
    },
    methods: {
      // 
      async getcallerBlackListsList() {
        try {
          let query = {
            admincompanyId:this.companyId,
            blackType: this.searchInput
          }
          //
          const res = await callerBlackListsList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.callerBlackListsData = [];
            // 
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              const tableItem = {
                blackType:item.blackType,
                blacklistsId:item.blacklistsId,
                createAt:item.createAt,
                describe:item.describe,
                prefix:item.prefix,
                updateAt:item.updateAt,
                uuid:item.uuid,
                vlanId:item.vlanId,
              }
              this.callerBlackListsData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('', err);
        }
      },
      handSaveParam(args){
          if(args.blackType == undefined || args.blackType === '')delete args.blackType;
         return args;
      },
      handleSizeChange(val) {
      },
      rebackEvent(){
            var args =  new Object();
            this.$emit('rebackEvent',args);
     },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getcallerBlackListsList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleCallerBlackListsSearch() {
        this.getcallerBlackListsList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");   
         }else{
            return '';
         }
      },
      //
      handleCallerBlackListsAdd() {
        var tempVlanId = this.$data.currentUserVlanId;
        if(this.companyId != undefined && this.companyId != ''){
            tempVlanId = parseInt(this.companyId);
        }
        this.callerBlackListsForm = {
          blackType: '',
          prefix: '',
          describe: '',
          vlanId: tempVlanId,
        };
        this.dialogFormVisible = true;
      },
      //
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('', '', {
            confirmButtonText: '',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //
              const result = await deletecallerBlackListsByIds(data);
              if (result.resultCode == '0') {
                that.getcallerBlackListsList();
                this.$message({
                  type: 'success',
                  message: '!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }
      },
      //
      handleCallerBlackListsDelete(index, row) {
        let that = this;
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //
            const result = await deletecallerBlackListsByIds(data);
            if (result.resultCode == '0') {
              that.getcallerBlackListsList();
              this.$message({
                type: 'success',
                message: '!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      handleCallerBlackListsEdit(index, row) {
        if (typeof row !== 'undefined') {
            this.callerBlackListsEditForm.blackType = row.blackType;
            this.callerBlackListsEditForm.prefix = row.prefix;
            this.callerBlackListsEditForm.describe = row.describe;
            this.callerBlackListsEditForm.uuid = row.uuid;
            this.callerBlackListsEditForm.vlanId = row.vlanId;
        }
        this.dialogFormEdit = true;
      },
      //
      handlecallerBlackListsCheck(index, row) {
        this.dialogFormCheck = true;
        this.$data.showCheck = row;
      },
      //  
      handleCallerBlackListsSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //
              var args = this.$refs[formName].model;
              var params = this.handSaveParam(args);
              const result = await savecallerBlackLists(params);
              if (result.resultCode == '0') {
                  if(formName == 'callerBlackListsForm'){//
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getcallerBlackListsList();
                  this.$message({
                     type: 'success',
                     message: ''
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: ''
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: ''
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: ''
             });
            return false;
          }
        });
      },

       // 
      setUploadParams(importUploadFile) {
        return {
          filedata: importUploadFile.name,
        };
      },
      //
      importSuccessCallback (res) {
          this.$data.blackImportDialogVisible = false;
          this.getcallerBlackListsList(); 
          this.$message({
                type: 'success',
                message: res.result,
            });
      },
    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>


